import resume from "../assets/Resume/Leigh Dahlin Resume.docx";

export default function Navbar() {
  const portfolioEl = document.querySelector(".portfolio")

  const viewPortfolio = function(){
    window.location.href = "./index.html"
  };
    return (
        <header id="nav" className="mb-auto no-display into-margin">
        <div id="header-div">
          <h3 className="float-md-start mb-0 portfolio" onClick = {viewPortfolio}>Portfolio</h3>
          <nav className="nav nav-masthead justify-content-center float-md-end">
            <a id="work" className="nav-link" href="/">Work</a>
            <a id="skills" className="nav-link" href="/">Skills</a>
            <a id="contact" className="nav-link" href="/">Contact</a>
            <a id="resume" className="nav-link" href={resume} target="blank">Resume</a>
          </nav>
        </div>
      </header>
    
    )
}